@import "../../App.scss";

.headerStyle {
  height: 600px;
  text-align: center;
  background: $secondColor;
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  @media (max-width: 1280px) {
    height: 600px;
  }
  @media (max-width: 1024px) {
    height: 500px;
  }
  @media (max-width: 540px) {
    height: 850px;

    .slideDisplayMobile {
      display: block;
    }

    .inforCarousel h1 {
      font-size: 1.25rem !important;
    }
    .colInfor {
      max-width: 100% !important;
      flex: 1 !important;
      padding: 10px 50px !important;
    }

    .colPicture {
      margin-left: 50px;

      .slidePicture {
        margin-top: 2rem;

        img {
          width: 100%;
          margin: 0 auto;
        }
      }
    }
  }
}

.customizeDots {
  .slick-dots {
    li {
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: transparent;
      border-color: transparent;
      border-width: 1px;
  
      &.slick-active {
        // height: 24px;
        // top: -10px;
        width: 6px;
        button {
          // width: 24px;
          // height: 24px;
          border-radius: 50%;
          background-color: transparent;
          border-color: $mainColor;
          border-width: 1px;
        }
      }
    }
  }

  .slick-dots
  li
  button::after {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: $mainColor;
  // left: 25%;
  // top: 25%;
}
}

